/**
 * @Descripttion: 工位机 - 开工登记
 * @version:
 * @Author: Lois
 * @Date: 2020-6-21
 * @LastEditors: Lois
 * @LastEditTime:  2020-6-21
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';

// 引入警告信息
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
import sHeader from '@/components/common/StationHeader/header.vue';
import HeaderAction from '@/components/common/StationHeader/headerAction.vue';
import record from '@/views/workStationMain/ProductEnd/record.vue';
// API
import {
  productionTaskGetPageProductionTaskEndByConditionAction,
  deviceRealProductionGetListByGroupIdAction,
  productionTaskEndGetProductionTaskStartRecordByProductionIdAction,
  productionTaskEndInsertProductionTaskEndRecordAction
} from '@/api/product';
// 文字常量
import { BTN_TEXT, MSG, PAGE_TITLE, TITLE, CONSTANTS_PRODUCT, WORK_STATION, STATUS_CODE } from '@/utils/constantsFile';
export default {
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    sHeader,
    HeaderAction,
    record
  },
  data() {
    var positiveNumber = (rule, value, callback) => {
      var x = /^\d+(?=\.{0,1}\d+$|$)/;// 正数
      if (!x.test(value)) {
        return callback(new Error('请输入正数'));
      } else if (Number(value) > 99999999 || Number(value) < 0) {
        return callback(new Error('请输入范围0-99999999的数字'));
      } else if (isNaN(value)) {
        return callback(new Error('请输入正数'));
      } else {
        if (this.showData.lineDevProduct[rule.field.split('.')[1]].manualRecordCount > value) {
          return callback(new Error('请大于开工数'));
        } else {
          return callback();
        }
      }
    };

    return {
      WORK_STATION: WORK_STATION,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      TITLE: TITLE,
      PAGE_TITLE: PAGE_TITLE,
      MSG: MSG,
      BTN_TEXT: BTN_TEXT,
      STATUS_CODE: STATUS_CODE,
      rulesUpdate: {
        value: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: positiveNumber },
          { pattern: /^[0-9.]+$/, message: '请输入正数' }
        ]
      },
      formRow: {},
      showData: {
        lineDevProduct: []
      },
      searchInput: '',
      realName: window.localStorage.getItem('realName').replace(/"/g, ''),
      showPage: 0,
      tableLoading: false,
      dialogVisible: false,
      finishAllNum: 0,
      startRecord: '',
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          fixed: 'left',
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button onClick={() => this.goInfo(row)}>
                    完工
                  </el-button>
                </div>
              ];
            }
          }
        },
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          width: 'auto',
          align: 'center',
          fixed: 'left'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          width: 'auto',
          align: 'center',
          fixed: 'left'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_PRODUCT.MATERIAL_CODE,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_PRODUCT.MATERIAL_NORMS,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'modelNumber',
          title: CONSTANTS_PRODUCT.MODULE_NUM,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'taskCount',
          title: CONSTANTS_PRODUCT.WORK_NUM,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'completeCount',
          title: CONSTANTS_PRODUCT.FINISH_NUM,
          width: 'auto',
          align: 'center'
        }
      ],

      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  computed: {
  },
  methods: {
    /**
   * @description: 执行table分页
   * @param currentPage 第几页
   * @param pageSize 一页几条
   * @return: 无
   * @author: CH
   */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description:查询数据
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;
        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const paramsMore = {};
        if (this.searchInput !== '') {
          paramsMore.conditionJson1 = [
            {
              column: 'taskCode',
              operator: 1,
              ptype: 'string',
              type: 'like',
              value: this.searchInput
            }
          ];
        }
        const res = await productionTaskGetPageProductionTaskEndByConditionAction(params, paramsMore);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    refreshTable() {
      this.tablePage.currentPage = 1;
      this.tablePage.pageSize = 10;
      this.findList();
    },
    resetTable() {
      this.searchInput = '';
      this.tablePage.currentPage = 1;
      this.tablePage.pageSize = 10;
      this.findList();
    },
    showRecord() {
      this.dialogVisible = false;
      this.showPage = 1;
    },
    goBack() {
      this.showPage = 0;
    },
    goInfo(row) {
      this.startRecord = '';
      this.finishAllNum = 0;
      this.dialogVisible = !this.dialogVisible;
      this.formRow = JSON.parse(JSON.stringify(row));
      this.prevStart(row);
    },
    /**
     * @description: 获取上一次开工数据
     * @return: 无
     * @author: CH
     */
    async prevStart(row) {
      try {
        const res = await productionTaskEndGetProductionTaskStartRecordByProductionIdAction({
          productionTaskId: row.productionTaskId
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showData.lineDevProduct = res.rows.map((item) => {
            item.value = 0;
            item.devAutoCount = 0;
            item.deviceName = '';
            item.computeFinish = 0;
            return item;
          });
          this.getLineDevProduct(row);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 根据分组id查询这个分组下的所有的设备为计数设备的实时产量
     * @return: 无
     * @author: CH
     */
    async getLineDevProduct(row) {
      try {
        const res = await deviceRealProductionGetListByGroupIdAction({
          groupId: row.groupId
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          for (var x in this.showData.lineDevProduct) {
            for (var y in res.rows) {
              if (this.showData.lineDevProduct[x].deviceCode === res.rows[y].deviceCode) {
                this.showData.lineDevProduct[x].devAutoCount = res.rows[y].todayValue;
                this.showData.lineDevProduct[x].deviceName = res.rows[y].deviceName;
              }
            }
          }
          var arr = [];
          for (var item in this.showData.lineDevProduct) {
            arr.push('[' + this.showData.lineDevProduct[item].deviceName + '] ' + this.showData.lineDevProduct[item].manualRecordCount);
          }
          this.startRecord = arr.join(' , ');
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitStart() {
      this.$refs.productForm.validate((valid) => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.productionTaskId = this.formRow.productionTaskId;
        form.inspectCount = this.formRow.inspectCount;
        form.ngCount = this.formRow.ngCount;
        form.completeCount = this.formRow.completeCount;
        form.taskStartId = this.showData.lineDevProduct[0].taskStartId;
        var startTaskDeviceProductionRecordList = [];
        for (var item in this.showData.lineDevProduct) {
          var json = {};
          json.taskStartRecordId = this.showData.lineDevProduct[item].taskStartRecordId;
          json.deviceCode = this.showData.lineDevProduct[item].deviceCode;
          json.autoRecordCount = this.showData.lineDevProduct[item].devAutoCount;
          json.manualRecordCount = this.showData.lineDevProduct[item].value;
          json.recordCountReport = this.showData.lineDevProduct[item].computeFinish;
          startTaskDeviceProductionRecordList.push(json);
        }
        const res = await productionTaskEndInsertProductionTaskEndRecordAction(form,
          startTaskDeviceProductionRecordList);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisible = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    toFocus(active) {
      this.active = active;
    },
    toBlur(e, active) {
      this.active = active;
      clearTimeout(this.setTimeOut);
      this.setTimeOut = setTimeout(() => {
        if (document.activeElement.id.indexOf('input') === -1) {
          this.active = document.activeElement.id;
        } else {
          this.active = '';
        }
      }, 150);
    },
    num(insertTxt) {
      if (this.active) {
        var elInput = document.getElementById(this.active);
        var startPos = elInput.selectionStart;
        var endPos = elInput.selectionEnd;
        if (startPos === undefined || endPos === undefined) return;
        var txt = elInput.value;
        if (insertTxt === '.') {
          if (txt.indexOf('.') !== -1) {
            return false;
          }
          if (startPos === 0) {
            return false;
          }
        }
        var result = txt.substring(0, startPos) + insertTxt + txt.substring(endPos);
        elInput.value = result;
        elInput.focus();
        this.$nextTick(() => {
          elInput.selectionStart = startPos + 1;
          elInput.selectionEnd = endPos + 1;
          this.showData.lineDevProduct[Number(this.active.replace('input', ''))].value = result;
          this.editFunc();
        });
      }
    },
    del() {
      if (this.active) {
        var elInput = document.getElementById(this.active);
        var startPos = elInput.selectionStart;
        var endPos = elInput.selectionEnd;
        if (startPos === undefined || endPos === undefined) return;
        var txt = elInput.value;
        var result = txt.substring(0, startPos - 1) + txt.substring(endPos);
        elInput.value = result;
        elInput.focus();
        this.$nextTick(() => {
          if (startPos === 0) {
            elInput.selectionStart = startPos;
            elInput.selectionEnd = endPos;
          } else {
            elInput.selectionStart = startPos - 1;
            elInput.selectionEnd = endPos - 1;
          }
          this.showData.lineDevProduct[Number(this.active.replace('input', ''))].value = result;
          this.editFunc();
        });
      }
    },
    editFunc() {
      this.$refs.productForm.validate((valid) => { });
      var num = 0;
      for (var x in this.showData.lineDevProduct) {
        if (this.showData.lineDevProduct[x].value > this.showData.lineDevProduct[x].manualRecordCount) {
          var numx = this.$options.filters.accSubtr(Number(this.showData.lineDevProduct[x].value), Number(this.showData.lineDevProduct[x].manualRecordCount));
          this.showData.lineDevProduct[x].computeFinish = numx;
          num = this.$options.filters.computedAdd(num, Number(this.showData.lineDevProduct[x].computeFinish));
        } else {
          var numy = Number(this.showData.lineDevProduct[x].value);
          this.showData.lineDevProduct[x].computeFinish = numy;
          num = this.$options.filters.computedAdd(num, Number(this.showData.lineDevProduct[x].computeFinish));
        }
      }
      this.finishAllNum = num;
    },
    /**
     * @description: 返回首页
     * @return void
    */
    goHome() {
      this.$router.replace('/workstation/home');
    }
  },
  mounted() {

  },
  created() {
    this.findList();
  },
  destroyed() {
  }
};
