/**
 * @Descripttion: 工位机 - 开工记录
 * @version:
 * @Author: Lois
 * @Date: 2020-6-21
 * @LastEditors: Lois
 * @LastEditTime:  2020-6-21
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';

// 引入警告信息
import { showWarningMessage } from '@/utils/message';
// 引入错误提示
import { showErrorNotify } from '@/utils/notify';
import sHeader from '@/components/common/StationHeader/header.vue';
import HeaderAction from '@/components/common/StationHeader/headerAction.vue';
// API
import {
  productionTaskEndGetPageProductionTaskEndRecordByCurrentUserAction
} from '@/api/product';
// 文字常量
import { BTN_TEXT, MSG, PAGE_TITLE, TITLE, CONSTANTS_PRODUCT, WORK_STATION, STATUS_CODE } from '@/utils/constantsFile';
export default {
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    sHeader,
    HeaderAction
  },
  data() {
    return {
      WORK_STATION: WORK_STATION,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      TITLE: TITLE,
      PAGE_TITLE: PAGE_TITLE,
      MSG: MSG,
      BTN_TEXT: BTN_TEXT,
      STATUS_CODE: STATUS_CODE,
      searchInput: '',
      realName: window.localStorage.getItem('realName').replace(/"/g, ''),
      showPage: 0,
      tableLoading: false,
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'endOperatorUserRealName',
          title: CONSTANTS_PRODUCT.END_USER,
          width: '120',
          align: 'center'
        },
        {
          field: 'endTime',
          title: CONSTANTS_PRODUCT.END_TIME,
          width: '180',
          align: 'center'
        },
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          width: '120',
          align: 'center'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          width: '120',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          width: '120',
          align: 'center'
        },
        {
          field: 'allCountReport',
          title: CONSTANTS_PRODUCT.ALL_COUNT_REPORT,
          width: '120',
          align: 'center'
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_COMPUTE_PRODUCT,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].recordCountReport);
            }
            return arr.join(' , ');
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_END_RECORD,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].manualRecordCount);
            }
            return arr.join(' , ');
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.USER_START_RECORD,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].startManualRecordCount);
            }
            return arr.join(' , ');
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_COMPUTE_PRODUCT,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].deviceRecordCountReport);
            }
            return arr.join(' , ');
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_END_RECORD,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].autoRecordCount);
            }
            return arr.join(' , ');
          }
        },
        {
          field: 'endTaskDeviceProductionRecordList',
          title: CONSTANTS_PRODUCT.DEV_START_RECORD,
          width: '300',
          align: 'center',
          formatter({ cellValue, row, column }) {
            var arr = [];
            for (var item in cellValue) {
              arr.push('[' + cellValue[item].deviceName + '] ' + cellValue[item].startAutoRecordCount);
            }
            return arr.join(' , ');
          }
        }
      ],

      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  methods: {
    /**
   * @description: 执行table分页
   * @param currentPage 第几页
   * @param pageSize 一页几条
   * @return: 无
   * @author: CH
   */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description:查询数据
     * @param {type}
     * @return {type}
     * @author: CH
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;
        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const paramsMore = {};
        if (this.searchInput !== '') {
          paramsMore.conditionJson1 = [
            {
              column: 'taskCode',
              operator: 1,
              ptype: 'string',
              type: 'like',
              value: this.searchInput
            }
          ];
        }
        const res = await productionTaskEndGetPageProductionTaskEndRecordByCurrentUserAction(params, paramsMore);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    refreshTable() {
      this.tablePage.currentPage = 1;
      this.tablePage.pageSize = 10;
      this.findList();
    },
    resetTable() {
      this.searchInput = '';
      this.tablePage.currentPage = 1;
      this.tablePage.pageSize = 10;
      this.findList();
    },
    goBack() {
      this.$emit('goBack');
    }
  },
  mounted() {

  },
  created() {
    this.findList();
  },
  destroyed() {
  }
};
